.App {
  text-align: center; }

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
  animation: App-logo-spin infinite 20s linear;
  height: 80px; }

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white; }

.App-title {
  font-size: 1.5em; }

.App-intro {
  font-size: large; }

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg); } }

.paper {
  background: url(./images/paper1.png) no-repeat;
  width: 758px;
  height: 800px;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  margin: 30px auto 30px 0px;
  padding: 47px 99px;
  display: -ms-flexbox;
  display: flex; }

.content {
  width: 100%;
  height: 100%;
  -webkit-transform: rotate(1.85deg);
  -ms-transform: rotate(1.85deg);
  transform: rotate(1.85deg);
  font-size: 14.91px; }

.stickers {
  width: 0px;
  margin-top: -65px; }
  .stickers .sticker {
    height: 100px; }
    .stickers .sticker div {
      position: absolute;
      width: 300px;
      height: 300px;
      -webkit-box-sizing: border-box;
      box-sizing: border-box;
      padding: 19px 19px 32px 32px; }
    .stickers .sticker a {
      display: block;
      height: 100%;
      -webkit-transform: rotate(-3deg);
      -ms-transform: rotate(-3deg);
      transform: rotate(-3deg);
      text-align: center;
      -webkit-transition: 0.3s;
      -o-transition: 0.3s;
      transition: 0.3s; }
    .stickers .sticker a:hover {
      background-color: rgba(0, 0, 0, 0.5); }



