.display {
  font-family: Led, 'Comic Sans MS', cursive;
  white-space: pre-wrap;
  text-transform: uppercase; }

table.mk52 {
  margin: 20px auto;
  background: #3F3F49;
  border-collapse: collapse;
  border-spacing: 0;
  -webkit-box-shadow: 10px 10px 20px black;
  box-shadow: 10px 10px 20px black;
  border-radius: 5px; }
  table.mk52 td {
    padding: 0; }
  table.mk52 .display {
    font-family: Led, 'Comic Sans MS', cursive;
    font-size: 19pt;
    color: yellow;
    white-space: pre-wrap;
    opacity: 1 !important;
    filter: "alpha(opacity=100)" !important; }
  table.mk52 .display.wait {
    opacity: 0.2 !important;
    filter: "alpha(opacity=20)" !important; }
  table.mk52 .mk_a0 {
    background: url(calc/a0.png) no-repeat;
    height: 66px; }
  table.mk52 .mk_a3 {
    background: url(calc/a3a.png) no-repeat 50% 0px, url(calc/a3.png) repeat-x;
    height: 66px; }
  table.mk52 .mk_a4 {
    background: url(calc/a4.png) no-repeat;
    width: 33px;
    height: 66px; }
  table.mk52 .mk_b0 {
    background: url(calc/b0.png) repeat-y;
    width: 40px; }
  table.mk52 .mk_b1 {
    background: url(calc/b1.png) no-repeat;
    width: 206px;
    height: 66px;
    text-align: center; }
  table.mk52 .mk_b2 {
    background: url(calc/b2.png) repeat-y;
    width: 55px; }
  table.mk52 .mk_b3 {
    vertical-align: top;
    background: #2F2B39;
    background-image: url(../images/noise.png); }
  table.mk52 .mk_b4 {
    background: url(calc/b4.png) repeat-y;
    width: 33px; }
  table.mk52 .mk_c0 {
    background: url(calc/c0.png) no-repeat;
    width: 40px;
    height: 41px; }
  table.mk52 .mk_c1 {
    background: url(calc/c1.png) repeat-x;
    width: 206px;
    height: 41px; }
  table.mk52 .mk_c2 {
    background: url(calc/c2.png) no-repeat;
    width: 55px;
    height: 41px; }
  table.mk52 .mk_d0 {
    background: url(calc/d0.png) repeat-y;
    width: 40px;
    height: 98px; }
  table.mk52 .mk_d1 {
    background: url(calc/d1.png) no-repeat, url(calc/d1b.png);
    width: 206px; }
  table.mk52 .mk_d2 {
    background: url(calc/d2.png) repeat-y;
    width: 55px; }
  table.mk52 .mk_e0 {
    background: url(calc/e0.png) no-repeat;
    width: 40px;
    height: 17px; }
  table.mk52 .mk_e1 {
    background: url(calc/e1.png) repeat-x;
    height: 17px; }
  table.mk52 .mk_e2 {
    background: url(calc/e2.png) no-repeat;
    width: 55px;
    height: 17px; }
  table.mk52 .mk_e3 {
    background: url(calc/e3.png) repeat-x;
    height: 17px; }
  table.mk52 .mk_e4 {
    background: url(calc/e4.png) no-repeat;
    width: 33px;
    height: 17px; }

.inset {
  border-radius: 7px;
  -webkit-box-shadow: inset 4px 4px 2px rgba(0, 0, 0, 0.5), 2px 2px 1px rgba(255, 255, 255, 0.25);
  box-shadow: inset 4px 4px 2px rgba(0, 0, 0, 0.5), 2px 2px 1px rgba(255, 255, 255, 0.25);
  padding: 5px;
  padding: 20px; }

.outset {
  border-radius: 7px;
  -webkit-box-shadow: 4px 4px 2px rgba(0, 0, 0, 0.5);
  box-shadow: 4px 4px 2px rgba(0, 0, 0, 0.5);
  padding: 5px; }



